<template>
  <div class="button_region button_st" 
    @click="return_event" 
    @mouseover="mouseOver"
    @mouseleave="mouseLeave" >
    返回
  </div>
  <div v-if="menu_visible">Test</div>
</template>

<script>
    export default {
        name: "return_component",
 
        data () {
          return {
            menu_visible: false,
          }
        },

        methods:{
          return_event() {
            this.$router.go(-1);
          },
          mouseOver:function(){
            this.menu_visible = true;
          },
          mouseLeave: function () {
            this.menu_visible = false;
          },
        }
 
    }
</script>
 
<style scoped>
.button_region {
  position: fixed;
  left: 88%;
  top: 85%;
  z-index: 5;

  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 20px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2), 0 3px 10px 0 rgba(0,0,0,0.19);
  border-radius: 80%;

}

.button_st:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.24),0 6px 15px 0 rgba(0,0,0,0.19);
}
</style>