import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Cardscrollbar from '../views/1_cardscrollbar.vue'
import npy from '../views/3_tmp.vue'


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{
      title:"MaoのHTML笔记",
      main_visible:true
    }
  },{
    path: '/1_cardscrollbar',
    name: '1_cardscrollbar',
    component: Cardscrollbar,
    meta:{
      title:"卡片示例",
      main_visible:false
    }
  },{
    path: '/3_tmp',
    name: '3_tmp',
    component: npy,
    meta:{
      title:"嘿嘿嘿",
      main_visible:false
    }
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
